<template>
<container-template title="最近文章" icon="el-icon-document" class="container">
<article-list-item v-for="(item,index) in list" :isFirst="index === 0" :info="item"></article-list-item>
</container-template>
</template>

<script>
import articleListItem from './articleListItem.vue'
  export default {
  components: { articleListItem },
  data() {
    return {
      list: null
    }
  },
    mounted () {
      this.$axios.get('/getRecentArticle').then(res => {
        this.list = res.data.data;
      })
    },
  }
</script>

