<template>
  <div class="item-container">
    <el-divider v-if="!isFirst" class="divider"></el-divider>
    <div class="container-title">
      <div class="title-text" @click="pushRoute()">{{info.title}}</div>
      <el-row class="detail">
        <el-col :span="8" class="col">
          <i class="el-icon-time" ></i>
          <span>{{time}}</span>
        </el-col>
        <el-col :span="5" class="col">
          <i class="el-icon-view"></i>
          <span>{{info.watch}}</span>
        </el-col>
        <el-col :span="5" class="col">
          <i class="el-icon-tickets"></i>
          <span>{{info.comment}}</span>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      time:null
    }
  },
  props: {
    isFirst: {
      type: Boolean,
      default: false
    },
    info: {
      type: Object
    }
  },
  mounted () {
    this.time = this.$moment(this.info.time).fromNow();
  },
  methods: {
    pushRoute() {
      this.$router.push({
        name:'Article',
        params:{
          id:this.info.id
        }
      })
    }
  },
}
</script>

<style lang="scss" scoped>
.item-container {
  .divider {
    margin: 10px 0;
  }
  .container-title{
    display: flex;
    flex-direction: column;
    font-size: 14px;
    text-align: left;
    .title-text{
      margin: 10px 0;
      cursor: pointer;
      margin-left: 8px;
      transition: all 0.5s ease; 
      &:hover {
        color: $hover;
      }
    }
    .detail {
      margin-left: 8px;
      .col {
        height: 14px;
        line-height: 14px;
      }
      span {
        margin-left: 2px;
      }
    }
  }
}
</style>