<template>
  <div class="container">
    <el-breadcrumb separator=">" class="container-bread">
      <el-breadcrumb-item v-for="item in list" :to="{ name: item.meta.name }">{{
        item.meta.breadNav
      }}</el-breadcrumb-item>
    </el-breadcrumb>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: null,
    };
  },
  watch: {
    $route(to, from) {
      this.getList();
    },
  },
  created() {
    this.getList();
  },
  mounted() {},
  methods: {
    getList() {
      let arr = this.$route.matched.filter((item) => item.meta);
      let list = [{ meta: { breadNav: "首页", name: "Main" } }];
      if (arr.length > 0) {
        if (arr[0].meta.name === "Main") {
          arr.shift();
        }
      }
      this.list = list.concat(arr);
      if (this.$route.name === "Article") {
        this.$axios
          .post("/TitleById", {
            id: this.$route.params.id,
          })
          .then((res) => {
            console.log('res',res);
            this.list.push({ meta: { breadNav: res.data.data, name: null } });
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  height: 48px;
  background-color: $white;
  .container-bread {
    height: 48px;
    line-height: 48px;
    padding: 0 20px;
  }
}
</style>