<template>
  <div class="nav">
    <div v-if="$store.state.app.device === 'mobile'">
      <nav-right mode="vertical" class="absolute mobile-nav" :class="{'nav-open':opened}" v-show="opened"/>
      <div class="mask" @click="closeSideBar()" v-show="$store.state.app.sidebar.opened"></div>

    </div>
    <el-row :gutter="0" v-if="$store.state.app.device === 'desktop'">
      <el-col :push="1" :span="6" class="logo">
        <logo />
      </el-col>
      <el-col :push="1" :span="12"><nav-right /></el-col>
      <el-col :span="6" class="logo">
        <search />
      </el-col>
    </el-row>

    <el-row :gutter="0" v-else justify="center" align="center">
      <el-col :span="6">
        <div class="menu-container" @click="toggleSideBar()">
          <i class="el-icon-menu" ></i>
        </div>
      </el-col>
      
      <el-col :span="12" class="logo">
        <logo />
      </el-col>

      <el-col :span="6" class="logo">
        <search />
      </el-col>
    </el-row>
  </div>
</template>

<script>
import logo from "./logo.vue";
import navRight from "./nav-right.vue";
import search from "./search.vue";
import ResizeMixin from "../mixin/ResizeHandler";
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      imgSrc: this.$config.logo,
      blogName: this.$config.blogName,
    };
  },
  computed: {
    ...mapGetters({
      opened: 'app/sideBarOpened'
    })
  },
  methods: {
    handleSelect() {},
    closeSideBar() {
      this.$store.dispatch("app/closeSideBar");
    },
    ...mapActions(
      {
        toggleSideBar: "app/toggleSideBar",
        // closeSideBar: "app/closeSideBar",
      }
    ),
  },
  mounted() {},
  mixins: [ResizeMixin],
  components: {
    logo,
    navRight,
    search,
  },
};
</script>

<style lang="scss" scoped>
.nav {
  position: fixed;
  z-index: 1000;
  background-color: $white;
  // height: 100px;
  width: 100%;
  // border-bottom: 1px solid $grey;
  // padding-left: 10%;
  margin: -8px;
  border-bottom: 1px solid $border-color;
  .logo {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.img {
  height: 50px;
}

.menu-container {
  display: flex;
  height: 60px;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: rgb(74, 74, 148);
}

.absolute{
   position: absolute;
   z-index: 1000;
   top: 0;
   left: 0;
   width: 0px;
   height: 100vh;
}

.mask {
  position: absolute;
  z-index: 100;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,.3);

}

.mobile-nav {
  transition: all ease .5s;
}

.nav-open {
  width: 150px;

}
</style>