<template>
  <container-template title="文件夹" icon="el-icon-folder">
    <list :listData="listData"></list>
  </container-template>
</template>

<script>
import list from "./list.vue";
export default {
  components: {
    list,
  },
  data() {
    return {
      listData: [
      ],
    };
  },
  mounted () {
    this.$axios.get('/getFileSystem').then(res => {
      this.listData = [res.data.data];
    });
  },
};
</script>

<style lang="scss" scoped>
.archive-container {
  margin-top: 8px;
  background-color: white;
  padding: 10px;
  .title {
    font-size: 16px;
    text-align: left;
    transform: translateX(10px);
    margin-bottom: 8px;
  }
}
</style>