import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import {Icon, Row, Col, Tree, Tag, Image, Divider, Backtop, Breadcrumb, BreadcrumbItem, Menu, MenuItem, MenuItemGroup,Timeline,TimelineItem, Avatar} from 'element-ui'

Vue.config.productionTip = false
import 'element-ui/lib/theme-chalk/index.css'
import 'element-ui/lib/theme-chalk/display.css'
Vue.use(Icon)
Vue.use(Row)
Vue.use(Col)
Vue.use(Tree)
Vue.use(Tag)
Vue.use(Image)
Vue.use(Divider)
Vue.use(Backtop)
Vue.use(Breadcrumb)
Vue.use(BreadcrumbItem)
Vue.use(Menu)
Vue.use(MenuItem)
Vue.use(MenuItemGroup)
Vue.use(Timeline)
Vue.use(TimelineItem)
Vue.use(Avatar)

import 'github-markdown-css/github-markdown.css'
import 'highlight.js/styles/idea.css'  //这里有多个样式，自己可以根据需要切换
import hljs from 'highlight.js'
// 如果开启了typescript 需要额外安装 npm install @types/highlight.js
// 通过 import * as hljs from 'highlight.js' 引入
Vue.directive('highlight', function (el) {
  const blocks = el.querySelectorAll('pre code')
  blocks.forEach(block => {
    hljs.highlightBlock(block)
  })
})
// 也可以把这自定义指令封装 通过Vue.use()，来注入

import config from '@/blog.config.js'
Vue.prototype.$config = config;

import containerTemplate from '@/components/containerTemplate/containerTemplate'
Vue.component(containerTemplate.name, containerTemplate)
import bottomToUp from '@/components/bottomToUp/bottomToUp'
Vue.component(bottomToUp.name, bottomToUp);

require('./mock/index.js')

import axios from 'axios'; /* 引入axios进行地址访问*/
Vue.prototype.$axios = axios;

import moment from 'moment'
Vue.prototype.$moment = moment;

import '@/common/js/zh-cn.js'


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')